* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    scroll-behavior: smooth;
}
body{
    background-color: #fcfcfc;
    font-family: "Cera Pro Medium";
}
li {
    list-style: none;
}
a {
    text-decoration: none;
}
p::selection,h1::selection,h2::selection,h3::selection,h4::selection,h5::selection,h6::selection,b::selection,span::selection,a::selection {
    background-color: #2A2D34;
    color: white;
}
img::selection{
    background-color: white;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}
main{
    margin: 0 6% 2rem 6%;
    padding-top: 8%
}
.ghost {
    width: 100%;
    height: 1.5rem;
    background-color: #fcfcfc;
}
/* Page Scroll*/
body::-webkit-scrollbar{
    width: 9px;
    background-color: #2A2D34;
}
body::-webkit-scrollbar-track {
  margin-top: 4.3rem;
  background: hsl(0, 0%, 90%);
}
body::-webkit-scrollbar-thumb {
  background-color: #2A2D34;
}
@media (max-width: 624px) {
    .ghost{
        height: 3rem;
    }
}
/* Fonts */
@font-face {
    src: url("./assets/fonts/Cera\ Pro\ Medium.otf");
    font-family: "Cera Pro Medium";
}